@font-face {
  font-family: "Pretendard";
  font-weight: 100;
  src: url("../src/styles/fonts/Pretendard-Thin.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Pretendard";
  font-weight: 300;
  src: url("../src/styles/fonts/Pretendard-Light.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Pretendard";
  font-weight: 400;
  src: url("../src/styles/fonts/Pretendard-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Pretendard";
  font-weight: 500;
  src: url("../src/styles/fonts/Pretendard-Medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Pretendard";
  font-weight: 700;
  src: url("../src/styles/fonts/Pretendard-Bold.woff2") format("woff2");
  font-display: swap;
}

html {
  scroll-behavior: smooth;
}

body,
th,
td,
input,
select,
textarea,
button {
  font-family: "Pretendard", Apple SD Gothic Neo, Malgun Gothic, sans-serif !important;
  /* font-family: "Inter", "Spoqa Han Sans Neo", Apple SD Gothic Neo, Malgun Gothic,
    sans-serif !important; */
  font-style: normal;
  font-weight: normal;

  line-height: 1.5;
  letter-spacing: -0.015em;
}

a {
  color: #000;
  text-decoration: none;
}

button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
}
